
  .swiper-wrapper {

    width: 52%;
    height: 50%;
    z-index: 0;
  }
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: transparent;
    position: relative;
    top: -5px;
  
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .swiper {
    width: 100%;
    height: 250px;
    margin: 2rem;
  }
  .append-buttons {
    text-align: center;
    margin-top: 20px;
  }
  
  .append-buttons button {
    display: inline-block;
    cursor: pointer;
    border: 1px solid #007aff;
    color: #007aff;
    text-decoration: none;
    padding: 4px 10px;
    border-radius: 4px;
    margin: 0 10px;
    font-size: 13px;
  }
  .swiper-button-prev,.swiper-button-next{

    top: 74px;
  }
  .swiper-slide {
    background: #fff0;
  }
  .swiper-button-prev, .swiper-button-next {
    background-color: azure;
    border-radius: 100px;
    width: 50px;
    height: 50px;

  }
  .swiper-button-next::after, .swiper-button-prev::after {

    font-size: large;
  }
 @media only screen and (max-width:800px) {

  .swiper {
margin: 0px;

  }
  .swiper-button-next::after, .swiper-button-prev::after {

    font-size: small;
  }
  .swiper-button-prev, .swiper-button-next {
    background-color: azure;
    border-radius: 100px;
    width: 40px;
    height: 40px;

  }
 }